import React from 'react';
import { SocialIcon } from 'react-social-icons';
import Pokedex from "../assets/images/pokedex.png";
import Home from "../assets/images/home.png";
import Web1 from"../assets/images/web1.png";
class Header extends React.Component {

    changeTheme = () => {

        // debugger
        const currentTheme = document.documentElement.getAttribute('data-theme');
        // console.log(currentTheme);

        let targetTheme = "light";

        if (currentTheme === "light") {
            targetTheme = "dark";

            this.setState({
                isChecked: true,
            })

            // console.log(targetTheme);
        } else {
            this.setState({
                isChecked: false,
            })
        }
        document.documentElement.setAttribute('data-theme', targetTheme)
    }
    
    render() {
        return (
            <>
                <div className="app__header">
                    <div className="switch">

                        <div className="toggle">
                            <label htmlFor="themeSwitch"></label>
                            <input type="checkbox" name="swich-theme" id="themeSwitch" onClick={this.changeTheme} defaultChecked />
                            <div className="toggle-bg"></div>
                            <div className="toggle-thumb">
                                <i className="fas fa-sun"></i>
                                <i className="fas fa-moon"></i>
                            </div>
                        </div>
                    </div>
                    <div className="poke__logos noselect">
                        <img src={Pokedex} alt="pokelogo" className="poke__logo" />
                    </div>
                    <img alt="home" herf="https://animemandir.github.io" src={Home}/>
                    <img alt="blog" herf="https://anime-mandir.blogspot.com" src={Web1}/>
                    <SocialIcon url="https://github/animemandir" />
                    <SocialIcon url="https://instagram.com/izumi_weebx" />
                    <SocialIcon url="https://т.website/s/aayush_s_c" network="telegram" />
                    <SocialIcon url="https://discord.gg/xP6kqFZ3" />
                </div>
            </>
        )
    }
}

export default Header;